import React, { useEffect, useState } from 'react'
import { Link, useLocation } from "react-router-dom";
import PersonIcon from '@material-ui/icons/Person';
import { Button } from '@material-ui/core';
import SidebarOption from './SidebarOption';
import "../styles/sidebar.css";
import { useStateValue } from "../StateProvider";
import { auth } from '../Firebase';
import { actionTypes } from '../Reducer';

function Sidebar() {
    const [{}, dispatch] = useStateValue();
    var location = useLocation()

    const routes =
        [
        {
            option: 'ARTIFICIAL INTELLIGENCE',
            key: 0,
            pathName: '/artificialintelligence',
            active: false,
        },
        {
            option: 'GRAPHIC DESIGNING',
            key: 1,
            pathName: '/graphicdesigning',
            active: false,
        },
        {
            option: 'NETWORKING',
            key: 2,
            pathName: '/networking',
            active: false,
        },
        {
            option: 'PROGRAMMING',
            key: 3,
            pathName: '/programming',
            active: false,
        },
        {
            option: 'ROBOTICS',
            key: 4,
            pathName: '/robotics',
            active: false,
        },
        {
            option: 'FAVOURITES',
            key: 5,
            pathName: '/favourites',
            active: false,
        }
        
    ]

    // useEffect(()=> {
    //     for(var i in routes) {
    //         if(location.pathname == routes[i].pathName) {
    //             routes[i].active = true
    //         }
    //         else {
    //             routes[i].active = false
    //         }
    //     }
    // }, [location])
       
    

    const [{user}] = useStateValue()
    return (
        <div className='drawer'>
            <div className='drawer-profile'>
                <PersonIcon className='userIcon' fontSize='large' />
                <div className='profile-email'>{user.email}</div>
                <Button 
                    variant='contained'
                    size='large'
                    className='logout-button'
                    color='secondary'
                    onClick={()=> {
                        auth
                            .signOut()
                            .then(() => {
                            dispatch({
                                type: actionTypes.REMOVE_USER,
                            }); 
                            })
                            .catch((error) => alert(error.message));
                        
                    }} 
                >
                    LOGOUT
                </Button>
            </div>
            <div>
                {routes.map((route)=> (
                    <Link className="sidebar-link" to={route.pathName} >
                        <SidebarOption option={route.option} key={route.key} id={route.pathName} active={route.active}/>
                    </Link>
                ))}
                
                
            </div>
        </div>
    )
}

export default Sidebar
