import React from 'react'
import Click from '../assets/none-selected.jpg'
import '../styles/noneSelected.css'

function NoneSelected() {
    return (
        <>
            <div className='noneSelected-main'>
                <div className='container'>
                    <p className='noneSelected-text'>
                        Click one of the fields to begin.
                    </p>
                    <img src={Click} alt='click' />
                </div>
            </div>
        </>
    )
}

export default NoneSelected
