import './App.css';
import Programming from './screens/Programming';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import GraphicDesigning from './screens/GraphicDesigning';
import Networking from './screens/Networking';
import Favourites from './screens/Favourites';
import NoneSelected from './screens/NoneSelected';
import Sidebar from './components/Sidebar';
import { useStateValue } from "./StateProvider";
import Login from './screens/Login';
import ArtificialIntelligence from './screens/ArtificialIntelligence';
import Robotics from './screens/Robotics';
import Header from './components/Header';

function App() {

  const [{ user }] = useStateValue();
  return (
    <Router>
      <div className="app">
      {!user ? (
        <Login />
      ) : (
        <>
        <Header />
        <div className='app-body'>
        <Sidebar />
        <Switch>
          <Route path="/graphicdesigning">
            <GraphicDesigning />
          </Route>
          <Route path="/networking">
            <Networking />
          </Route>
          <Route path="/programming">
            <Programming />
          </Route>
          <Route path="/favourites">
            <Favourites />
          </Route>
          <Route path="/artificialintelligence">
            <ArtificialIntelligence />
          </Route>
          <Route path="/robotics">
            <Robotics />
          </Route>
          <Route path="/">
            <NoneSelected />
          </Route>
        </Switch>
        </div>
        </>
      )}
      </div>
    </Router>
  );
}

export default App;
