import React, { useEffect, useState } from 'react'
import VideoTab from '../components/VideoTab'
import db from '../Firebase';
import '../styles/main.css'
import { useStateValue } from '../StateProvider';

function Programming() {

    const [video, setVideo] = useState([]);
    const [resources, setResources] = useState([])
    const [{user}] = useStateValue()
    const [faveVids, setFaveVids] = useState([])
    
    const CheckFave= (vidId)=> {
        let vidIds = faveVids.map(fv=> fv.videoId)
        if(vidIds.includes(vidId))
        return true
        else return false
    }
    
    useEffect(() => {
        db.collection('favourite-vids').doc(user.email).set({
            userName: user.displayName
        })
        db.collection("programming-vids")
        .orderBy("id", "asc")
        .onSnapshot((snapshot) => {
            setVideo(snapshot.docs.map((doc) => doc.data()));
        });
        
        db.collection('resources').doc('programming')
        .collection('programming-resources')
        .onSnapshot((snapshot)=> {
            setResources(snapshot.docs.map((doc)=> doc.data()))
        }) 
        db.collection("favourite-vids").doc(user.email).collection('vid-id')
        .onSnapshot((snapshot) => {
        setFaveVids(snapshot.docs.map((doc) => doc.data()));
        });
    }, [user.email, user.displayName]);

    return (
            <div className='main'>
                <div className='main-container'>
                    <div className='scrollGroup'>
                        {video.map((vid)=> (
                            <VideoTab 
                            id= {vid.id}
                            videoId= {vid.videoId}
                            isFave={CheckFave(vid.videoId)}/>
                        ))}
                    </div>
                    <section className='resourcesSection'>
                        <h2> RELATED RESOURCES </h2>
                        {resources.map((resource)=> (
                            <a target='_blank' rel="noreferrer" href={resource.link}>{resource.title}</a>
                        ))}
                        <div> © 2021 | ALL RIGHTS RESERVED</div>
                    </section>
                </div>
            </div>
        
    )
}

export default Programming
