import React from 'react'
import FavoriteBorderOutlinedIcon from '@material-ui/icons/FavoriteBorderOutlined';
import FavoriteIcon from '@material-ui/icons/Favorite';
import TwitterIcon from '@material-ui/icons/Twitter';
import EmailIcon from '@material-ui/icons/Email';
import FacebookIcon from '@material-ui/icons/Facebook';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';

import {
    EmailShareButton,
    FacebookShareButton,
    TwitterShareButton,
    WhatsappShareButton,
  } from "react-share";

import { Divider } from '@material-ui/core';
import '../styles/videoTab.css'
import YouTube from 'react-youtube';
import db from "../Firebase";
import firebase from 'firebase'

import { useStateValue } from '../StateProvider';

function VideoTab({id, videoId, isFave}) {

    const [{user}] = useStateValue()

    const shareDetails = {title:'I just found this amazing video on video Repo, check it out', url: 'https://www.youtube.com/watch?v=jc9_Bqzy2YQ'}

    const addFavourite = (e, id)=> {
        e.preventDefault()
        db.collection('favourite-vids').doc(user.email).collection('vid-id').doc(id).set({
                videoId: id,
                timestamp: firebase.firestore.FieldValue.serverTimestamp()
            })
    }
    const removeFavourite = (e, id)=> {
        e.preventDefault()
        db.collection('favourite-vids').doc(user.email).collection('vid-id').doc(id).delete()
    }
    
    return (
        <div className='videoTab'>
            <div className='videoTab-container'>
            <YouTube
                videoId={videoId}
                id={id}
                className='video'
                opts={{height: '410', width: '660'}} 
            />
            </div>
            <div className='videoTab-icons'>
                    {isFave ? 
                    <FavoriteIcon color='secondary' className='faveIcon' onClick={(e)=> removeFavourite(e, videoId)} id={videoId}/>
                     : 
                    <FavoriteBorderOutlinedIcon color='secondary' className='faveIcon' onClick={(e)=> addFavourite(e, videoId)} id={videoId}/>}
                    <EmailShareButton subject={shareDetails.title} url={shareDetails.url} >
                        <EmailIcon className='shareIcon' size={"2rem"} />
                    </EmailShareButton>
                    <TwitterShareButton title={shareDetails.title} url={shareDetails.url}>
                        <TwitterIcon className='shareIcon' style={{ color: 'skyblue' }} size={"2rem"} />
                    </TwitterShareButton>
                    <FacebookShareButton quote={shareDetails.title} url={shareDetails.url}>
                        <FacebookIcon className='shareIcon' style={{ color: 'blue' }} size={"2rem"} />
                    </FacebookShareButton>
                    <WhatsappShareButton title={shareDetails.title} url={shareDetails.url} searator="::">
                        <WhatsAppIcon className='shareIcon' style={{ color: 'limegreen' }} size={"2rem"} />
                    </WhatsappShareButton>  
            </div>
            <Divider className='videoTab-divider' />
        </div>
    )
}

export default VideoTab
