import React from 'react'
import '../styles/header.css'
import {Menu} from '@material-ui/icons'

function Header() {
    return (
        <div className='header'>
            <span className='header-tag'>VIDEO REPO</span>
            <span className='header-link'>Graphic Designing</span>
            <span className='header-link'>AI</span>
            <span className='header-link'>PROGRAMMING</span>
            <span className='header-link'>ROBOTICS</span>
            <span className='header-link'>NETWORKING</span>
            <span className='header-link'>FAVE</span>
            <Menu className='header-menu' style={{color: 'wheat'}}/>
        </div>
    )
}

export default Header
