import React from 'react'
import ClickFave from '../assets/video-repo.JPG'
import '../styles/nofavourites.css'

function NoFavourites() {
    return (
        <>
            <div className='noFavourite-main'>
                <div className='container'>
                    <p className='noFavourite-text'>
                        When you favourite videos, they appear here
                    </p>
                    <img src={ClickFave} alt='clickFave' />
                </div>
            </div>
        </>
    )
}

export default NoFavourites
