import { Divider } from '@material-ui/core'
import React from 'react'
import '../styles/sidebarOption.css'

function SidebarOption({option, id, active}) {

    
    return (
        <div className='sidebarOption' id={id}  style={{backgroundColor: active? 'white' : 'wheat'}} >
            <h2 className='sidebarOption-text'>{option}</h2>
            <Divider />
        </div>
    )
}

export default SidebarOption
