import React, { useEffect, useState } from 'react'
import db from '../Firebase';
import { useStateValue } from '../StateProvider';
import VideoTab from '../components/VideoTab'
import "../styles/main.css";
import "../styles/nofavourites.css";
import NoFavourites from './NoFavourites';
import { useLocation } from 'react-router';

function Favourites() {

    const [video, setVideo] = useState([]);
    const [resources, setResources] = useState([])
    const [{user}] = useStateValue()
    const [empty, setEmpty] = useState()

    var location = useLocation

    useEffect(()=> {
      if(video.length === 0) {
        setEmpty(true)
      }
      else {setEmpty(false)}
    }, [video])
    
    const CheckFave= (vidId)=> {
        let vidIds = video.map(v=> v.videoId)
        if(vidIds.includes(vidId))
        return true
        else return false
    }
    
  useEffect(() => {
    db.collection("favourite-vids").doc(user.email).collection('vid-id')
      .orderBy("timestamp", "desc")
      .onSnapshot((snapshot) => {
        setVideo(snapshot.docs.map((doc) => doc.data()));
      });
  }, []);
    return (
        <>
            <div className='main'>
              {empty? (
                <NoFavourites />
              ) 
              :
              (<div className='scrollGroup'>
              {video.map((vid)=> (
                          <VideoTab 
                          id= {vid.id}
                          videoId= {vid.videoId}
                          isFave={CheckFave(vid.videoId)}/>
                      ))}
              </div>)}
            </div>
        </>
    )
}

export default Favourites
